import Button from "../Button";
import Heading from "../Heading";

const Description = ({ data }) => {

    return(
        <>
            <div id="openModalWindow">
                <section className="close" title="Закрыть описание" id="closeModalWindow">&times;</section>
                <div className="info">
                    <img id="img" src={data.Logo} alt="" />
                    <h3 id="h3">{data.NameApp}</h3>
                    <p>
                        <span id="spanDescriptionPos">
                            <Heading data={data.Heading}/>
                        </span>
                        Если у вас есть вопросы, обращайтесь в
                        <a href="#Support" title="Перейти в поддержку" className="info-a close" id="supportCloseModal">Поддержку</a>.
                    </p>
                    <div className="button__modal" id="loremPos">{data.Button.map((i) => (<Button data={i}/>))}</div>
                </div>
            </div>
        </>
    );
}

export default Description;
