const Header = () => {
    window.addEventListener("scroll", () => {
        const navbarPos = window.scrollY;
        if (navbarPos > 10) {
            document.getElementsByClassName("navbar")[0].classList.add("nav_mini");
        } else {
            document.getElementsByClassName("navbar")[0].classList.remove("nav_mini");
        }
    });

    function showSidebar() {
        const sidebar = document.querySelector(".sidebar");
        sidebar.style.display = "flex";
    }

    function hideSidebar() {
        const sidebar = document.querySelector(".sidebar");
        sidebar.style.display = "none";
    }

    return (
        <nav className="navbar">
            <ul className="sidebar">
                <li onClick={hideSidebar} className="nav_close">
                    <a href="#">&times;</a>
                </li>
                <li onClick={hideSidebar}>
                    <a title="Перемещение по сайту" href="#home">
                        <span>Дом</span>
                    </a>
                </li>
                <li onClick={hideSidebar}>
                    <a title="Перемещение по сайту" href="#ourGames">
                        <span>Наши игры</span>
                    </a>
                </li>
                <li onClick={hideSidebar}>
                    <a title="Перемещение по сайту" href="#gamesInDevelopment">
                        <span>Игры в разроботке</span>
                    </a>
                </li>
                <li onClick={hideSidebar}>
                    <a title="Перемещение по сайту" href="#Support">
                        <span>Поддержка</span>
                    </a>
                </li>
            </ul>
            <ul>
                <li>
                    <img src="/icons/icon.png" alt="" className="shadow" />
                </li>
                <li className="hideOnMobile">
                    <a title="Перемещение по сайту" href="#home">
                        <span>Дом</span>
                    </a>
                </li>
                <li className="hideOnMobile">
                    <a title="Перемещение по сайту" href="#ourGames">
                        <span>Наши игры</span>
                    </a>
                </li>
                <li className="hideOnMobile">
                    <a title="Перемещение по сайту" href="#gamesInDevelopment">
                        <span>Игры в разроботке</span>
                    </a>
                </li>
                <li className="hideOnMobile">
                    <a title="Перемещение по сайту" href="#Support">
                        <span>Поддержка</span>
                    </a>
                </li>
                <li>
                    <h2>Ultanza</h2>
                </li>
                <li className="menu-button nav_open" onClick={showSidebar}>
                    <a href="#">&#9776;</a>
                </li>
            </ul>
        </nav>
    );
};

export default Header;
