const Heading = ({ data }) => {
    return(
        <>
            {data.map((i) => (
                <>
                    <b className="heading">{i.Headings}</b>
                    <br/>
                        <span>{i.Texts}</span>
                    <br/>
                    <br/>
                </>
            ))}
        </>
    );
};

export default Heading;