import GamesCart from "../GamesCart";
import Description from "../Description";

import React, { useState, useEffect } from "react";

import { initializeApp } from "https://www.gstatic.com/firebasejs/10.12.2/firebase-app.js";
import { getDatabase, ref, get } from "https://www.gstatic.com/firebasejs/10.12.2/firebase-database.js";

const Games = () => {
    const firebaseConfig = {
        apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
        authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
        projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
        storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
        messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
        appId: process.env.REACT_APP_FIREBASE_APP_ID,
    };

    const app = initializeApp(firebaseConfig);

    const db = getDatabase(app);
    const dbRef = ref(db);

    const [arroy, setArroy] = useState([]);

    useEffect(() => {
        get(dbRef, "web/app/").then((snapshot) => {
            if (snapshot.exists()) {
                const data = snapshot.val();

                setArroy(Object.values(data.web.app));
            } else {
                console.log("No data available");
            }
        }).catch((error) => {console.error(error);});
    }, [dbRef]);

    return (
        <>
            <h5 className="seprator" id="ourGames">Наши игры:</h5>

            <section className="guarantee" id="warehouse" title="Открыть описание">
                {arroy.map((i) => (i.ID === "test" ? <></> : <GamesCart data={i} />))}
            </section>

            <h5 className="seprator" id="gamesInDevelopment">Игры в разработке:</h5>

            <section className="guarantee" id="warehouse-test" title="Открыть описание">
                {arroy.map((i) => (i.ID === "test" ? <GamesCart data={i} /> : <></>))}
            </section>

            <section id="informationStorageLocation">
                {arroy.map((i) => (<Description data={i}/>))}
            </section>
        </>
    );
};

export default Games;
